import { Component } from '@angular/core';
import { NavController, NavParams, IonicPage } from 'ionic-angular';
import { Values } from '../../../providers/service/values';
import { Service } from '../../../providers/service/service';
import { Functions } from '../../../providers/service/functions';
//import { CheckoutOption } from '../checkout-option/checkout-option';


@IonicPage()
@Component({
  templateUrl: 'checkout-forgotten.html',
})
export class CheckoutForgotten {

  href: any;
  loadForgotten: any;
  forgottenData: any;
  public disableSubmit: boolean = false;

  constructor(public nav: NavController, public service: Service, params: NavParams, public functions: Functions, public values: Values) {

  	this.href = params.data;
    this.loadForgotten = null;
    this.forgottenData = [];
    
    this.service.loadForgotten(this.href)
    .then((results) => this.loadForgotten = results);
  }

 
  forgotten(email) {
    this.disableSubmit= true;
  	this.service.forgotten(this.href, email)
    .then((results) => this.handleResults(results));//loadForgotten = results);     
  }

    
  handleResults(results){
    this.disableSubmit= false;
    if(results.success){
      this.nav.setRoot('CheckoutOption')
      .then((results) => this.handleLogin(results));
    }  

    else if(results.error_warning){
      this.functions.showAlert(this.values.lan.text_error, results.error_warning);
    }
  }  

 
  handleLogin(results){
    this.functions.showAlert(this.values.lan.text_success, "An email with a confirmation link has been sent your email address.");
  }  
 
}





